*{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;
  background-color: #1A1A1A;
  font-family: GT Walsheim Pro;
  max-width: 1440px;
  margin: 0 auto;
}

img{
  max-width: 100%;
}


/* Navbar.js styling starts from here */


header .container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
 
}

header .container ul{
  display: flex;
  width: 500px;
  justify-content: space-between;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #FFFFFFB3;
}

.developers{
  font-weight: bold;
  color: #FFFFFF;
}

.motion{
  color: #FFFFFF;
  font-weight: 500;
}

.backToFramer{
  font-size: 15px;
}

header hr{
  color: #FFFFFFB3;
  opacity: 0.1;
}

/* FirstSection.js styling starts from here */

.firstSection{
  position: relative;
}

.firstSection .container{
  display: flex;
  flex-direction: column;
  height: 885px;
  margin-left: 12.4em;
}

.firstSection h1{
  font-size: 116px;
  font-weight: bold;
  margin: 0 auto;
}

.firstSection .framer{
  margin: 1em auto 0 auto;
  color: #FFFFFF;
}

.firstSection .motion{
  background: -webkit-linear-gradient(#72f, #c1b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.firstSection p{
  width: 500px;
  font-size: 28px;
  color: #FFFFFF;
  line-height: 38px;
  font-weight: 500;
}

.firstSection a {
  font-size: 28px;
  font-weight: 500;
  color: #DD22CC;
}

.github-tag{
  margin-left: 1em;
}

.firstSection .outer-box{
  width: 239px;
  height: 451px;
  background-color: #212121;
  border-radius: 25px;
  position: absolute;
  top: 270px;
  left: 760px;
  transform: rotate(-2deg);
  cursor: pointer;
}

.firstSection .inner-box{
  width: 214px;
  height: 426px;
  background: linear-gradient(40deg,#f108a4,#bd19c8);
  border-radius: 20px;
  position: absolute;
  top: 281.5px;
  left: 772.5px;
  transform: rotate(-2deg);
  cursor: pointer;
}

.small-box-one, .small-box-two, .small-box-three, .small-box-four, .small-box-five{
  width: 46px;
  height: 46px;
  background-color: #FFFFFF;
  border-radius: 10px;
  position: absolute;
  top: 298px;
  left: 785px;
  transform: rotate(-2deg);
}

.small-box-two{
  left: 851px;
  top: 295px;
}

.small-box-three{
left: 917px;
top: 292px;
}

.small-box-four{
top: 360px;
left: 787px;
}

.small-box-five{
  top: 358px;
  left: 853px;
}


/* SecondSection.js styling starts from here */

.secondSection-container{
  margin-left: 12.4em;
  margin-bottom: 4em;
}

.secondSection-container h1{
  font-size:80px;
  color: #FFFFFF;
  margin: 0;
}

.secondSection-container p {
  font-size: 28px;
  color: #FFFFFF;
  width: 520px;
}

.secondSection-container a{
  font-size: 28px;
  color: #DD22CC;
}

.declarative-text{
  background: -webkit-linear-gradient(#b0d, #c1b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.interaction-div{
  width: 1200px;
  height: 648px;
  border-radius: 15px;
  margin: 0 auto 4em auto;
  position: relative;
  
}

.interaction-preview{
  width: 880px;
  height: 648px;
  background: linear-gradient(135deg,#e09,#d0e);
  border-radius: 15px 0 0 15px;
}


.interaction-square{
  width: 200px;
  height: 200px;
  background-color: #FFFFFF;
  border-radius: 15px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 300px;
  margin: auto;
}


.interaction-code-div{
  width: 320px;
  height: 648px;
  background-color: #0F0F0F;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0 15px 15px 0;
}

.interaction-code{
  width: 320px;
  height: 350px;
  padding: 2em;
}

.interaction-code span{
  margin: 0;
  font-size: 15px;
  line-height: 1.6;
  letter-spacing: 0.5px;
}

.interaction-parameters{
  width: 320px;
  height: 298px;
  border-top: 2px solid #1A1A1A;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.interaction-parameters label{
  font-size: 15px;
  color: #FFFFFF;
  width: 46px;
  cursor: pointer;
}

.interaction-parameters div{
  display: flex;
  justify-content: space-between;
  width: 260px;
  align-items: center;
}

.interaction-parameters .number-input{
  width: 76px;
  height: 40px;
  color: #FFFFFF;
  background-color: #303030;
  font-size: 15px;
  border-radius: 10px;
  border-style: none;
  text-align: center;
  cursor: pointer;
}

input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.interaction-parameters .range-input{
  width: 76px;
  height: 36px;
  cursor: pointer;
}

.interaction-code .spanParameter{
  color: #FFCC66;
}

.interaction-code .spanValue{
  color: #FF9977;
}

.interaction-code .spanSyntax {
  color: #999999;
}

.interaction-code .animate{
  color: #AADDFF;
}

